@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);
* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
}

#root {
	overflow: auto;
	width: fit-content(100vw);
}
body {
	margin: 0;
	font-family: 'Noto Sans JP', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow: auto;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

@media only screen and (max-width: 345px) {
  .App {
    /* overflow: scroll; */
    width: fit-content(100vw);
  }
}

h1 {
  font-size: xx-large;
}

h2 {
  font-size: x-large;
}

h3 {
  font-size: larger;
}

h4 {
  font-size: large;
}

h5 {
  font-size: medium;
}

h6 {
  font-size: small;
}

.App {
  min-width: 360px;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  justify-items: center;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-align-content: stretch;
          align-content: stretch;
}

nav {
  -webkit-flex: 0 1 auto !important;
          flex: 0 1 auto !important;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: xx-large;
}
.nav-brand {
  letter-spacing: 0.2rem;
}

section.header-strip {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  justify-items: stretch;
}

section.header-strip > * {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
  -webkit-align-self: center;
          align-self: center;
  justify-self: center;
  min-width: 0px;
}

section.svg {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
}

svg {
  max-width: 480px;
  max-height: 480px;
}

section.svg > * {
  mix-blend-mode: luminosity;
  max-width: 360px;
  -webkit-align-self: center;
          align-self: center;
}

section.controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: stretch;
          align-items: stretch;
  text-align: start;
  padding: 1rem;
  grid-gap: 2rem;
  gap: 2rem;
}

section.controls > * {
  -webkit-flex: 1 0;
          flex: 1 0;
  min-width: 0px;
}

.tag {
  -webkit-flex: 0 1 auto !important;
          flex: 0 1 auto !important;
}

.input-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: stretch;
          align-items: stretch;
  background-color: aliceblue;
  border-radius: 5rem;
  padding: 0.25rem;
}

.input-box > input[type="text"] {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  mix-blend-mode: luminosity;
}
.input-box > button {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
}

.color-controls {
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: center;
          align-content: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.color-controls > button {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
  font-size: large;
}

i {
  font-size: large;
  text-align: center;
}

button {
  position: relative !important;
}

button:focus {
  outline: none;
}

button > .tooltip {
  display: none;
  position: absolute;
  width: -webkit-max-content;
  width: max-content;
  z-index: 3;
  padding: 0.5rem 1rem;
  top: -69%;
  left: -45%;
  background-color: rgba(0, 0, 0, 1);
  border-radius: 1rem;
  font-size: small;
  color: ivory;
}

.tooltip::after {
  content: "";
  width: 5px;
  height: 5px;
  padding: 10px;
  background-color: inherit;
  -webkit-transform: rotateZ(45deg);
          transform: rotateZ(45deg);
  left: 40%;
  right: 0px;
  bottom: 0px;
  border-radius: 0px;
  position: absolute;
  z-index: -1;
}

button:hover .tooltip {
  display: block;
}

section.palette {
  margin: 0px !important;
  align-self: stretch;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: stretch;
          align-content: stretch;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.pallete-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 1rem;
}

.palette-item-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: stretch;
          align-items: stretch;
  border-radius: 20px;
  overflow: hidden;
  background-color: ivory;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.palette-controls-container {
  display: none;
  position: absolute;
}
.palette-controls-container > * {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}
.pallete-colors {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  border-radius: inherit;
  width: 12rem;
  min-height: 18rem;
  letter-spacing: 2px;
}

.pallete-colors > * {
  -webkit-align-items: center !important;
          align-items: center !important;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  align-items: center;
}

.pallete-item-primary {
  grid-area: primary;
  -webkit-flex: 6 1 !important;
          flex: 6 1 !important;
}

.pallete-item-secondary {
  grid-area: secondary;
  -webkit-flex: 3 1 auto !important;
          flex: 3 1 auto !important;
}

.pallete-item-accent {
  grid-area: accent;
  -webkit-flex: 1 1 auto !important;
          flex: 1 1 auto !important;
}

footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 1rem;
  width: fit-content(100vw);
}

input[type="text"] {
  padding: 1rem;
  font-size: large;
  border-radius: 2rem;
  outline: none;
  border: none;
  background-color: inherit;
}

button {
  border-radius: 2rem;
  padding: 0.8rem;
  font-size: medium;
  border: none;
}

button:hover {
  -webkit-filter: opacity(0.85);
          filter: opacity(0.85);
}

.circle {
  border-radius: 50%;
  min-width: 3.4em;
  min-height: 3.4em;
  text-align: center;
}

a:link {
  text-decoration: none;
  color: unset;
}

a:visited {
  text-decoration: none;
  color: unset;
}

a:hover {
  text-decoration: underline;
  color: unset;
}

a:active {
  text-decoration: underline;
  color: unset;
}

@-webkit-keyframes rotateExit {
  from {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

@keyframes rotateExit {
  from {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

@-webkit-keyframes rotateEntry {
  from {
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
  }
  to {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}

@keyframes rotateEntry {
  from {
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
  }
  to {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}

